/*******************************
         Site Overrides
*******************************/
.font-weight-light {
  font-weight: 300 !important;
}

.font-weight-lighter {
  font-weight: lighter !important;
}

.font-weight-normal {
  font-weight: 400 !important;
}

.font-weight-bold {
  font-weight: 700 !important;
}

.font-weight-bolder {
  font-weight: bolder !important;
}

.font-italic {
  font-style: italic !important;
}

.font-normal {
  font-style: normal !important;
}

.btn-group,
.btn-group-vertical {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.1);
  border-radius: calc(4px - 1px);
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.btn-group:hover,
.btn-group-vertical:hover {
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 4px 20px 0 rgba(0, 0, 0, 0.1);
}
.btn-group:focus, .btn-group.focus,
.btn-group-vertical:focus,
.btn-group-vertical.focus {
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 4px 20px 0 rgba(0, 0, 0, 0.1);
}
.btn-group:active, .btn-group.active,
.btn-group-vertical:active,
.btn-group-vertical.active {
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 4px 20px 0 rgba(0, 0, 0, 0.1);
}
.btn-group:active:focus, .btn-group.active:focus,
.btn-group-vertical:active:focus,
.btn-group-vertical.active:focus {
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2), 0 4px 20px 0 rgba(0, 0, 0, 0.1);
}
.btn-group:disabled, .btn-group.disabled, fieldset:disabled .btn-group,
.btn-group-vertical:disabled,
.btn-group-vertical.disabled,
fieldset:disabled .btn-group-vertical {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.1);
  border: 0;
}
.btn-group > .btn,
.btn-group-vertical > .btn {
  box-shadow: none;
}
.btn-group > .btn-group,
.btn-group-vertical > .btn-group {
  box-shadow: none;
}
.btn-group > .btn-link:first-child,
.btn-group-vertical > .btn-link:first-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.btn-group > .btn-link:last-child,
.btn-group-vertical > .btn-link:last-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.nav-tabs {
  border-bottom: 0;
}
.nav-tabs .nav-link {
  border-width: 0 0 2px 0;
  border-style: solid;
  border-color: transparent;
  border-radius: 0;
  text-transform: uppercase;
  line-height: 1;
  font-weight: 500;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.6);
  padding: 17px 29px 16px 29px;
}
.nav-tabs .nav-link:hover {
  background-color: #f5f5f5;
  border-color: transparent;
}
.nav-tabs .nav-link:focus {
  border-color: transparent;
}
.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link {
  color: #1266f1;
  border-color: #1266f1;
}

.nav-pills {
  margin-left: -0.5rem;
}
.nav-pills .nav-link {
  border-radius: 0.25rem;
  font-size: 12px;
  text-transform: uppercase;
  padding: 17px 29px 16px 29px;
  line-height: 1;
  background-color: #f5f5f5;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.6);
  margin: 0.5rem;
}
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  color: #fff;
  background-color: #1266f1;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.1);
}

.navbar {
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.05);
  padding-top: 0.5625rem;
}

.navbar-toggler {
  border: 0;
}
.navbar-toggler:focus {
  box-shadow: none;
}

